@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-off-white text-zinc-850;
    font-feature-settings: "rlig" 1, "calt" 1;
    font-family: "Roboto Mono", -apple-system, BlinkMacSystemFont, "Segoe UI",
      "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  a {
    @apply text-blue-400 hover:underline hover:cursor-pointer;
  }
  h1 {
    @apply text-xl sm:text-4xl;
  }
  h2 {
    @apply text-lg sm:text-xl;
  }
  p {
    @apply text-xs leading-relaxed;
  }
}

@layer components {
  /* general components */
  .icon-link {
    @apply rounded-full w-10 h-10 bg-zinc-100 flex items-center justify-center transition duration-300 ease-in-out hover:scale-110;
  }
  .container {
    @apply rounded-md shadow-md p-6 dark:border-2 dark:border-zinc-850;
  }
  .pill {
    @apply text-xs font-bold rounded-full bg-violet-400 text-white px-2 py-1 shadow-md;
  }
  .btn-primary {
    @apply text-sm disabled:pointer-events-none rounded-md p-2 shadow-md bg-indigo-500 hover:bg-indigo-600 disabled:opacity-50 text-white transition;
  }
  .btn-danger {
    @apply text-sm disabled:pointer-events-none rounded-md p-2 shadow-md bg-red-500 hover:bg-red-600 text-white transition;
  }
  .btn-secondary {
    @apply text-sm disabled:pointer-events-none disabled:opacity-50 bg-zinc-200 hover:bg-zinc-300 rounded-md p-2 shadow-md dark:bg-zinc-700 dark:hover:bg-zinc-600 transition;
  }
  .alternating-bg {
    @apply even:bg-zinc-50 even:dark:bg-zinc-700 bg-zinc-100 dark:bg-zinc-800;
  }
}
